import React, { useEffect, useState } from "react";
import { Modal, Table, Button, Tooltip, Row, Progress } from "antd";
import Utils from "../../../../../utils/index";
import { SaveOutlined } from "@ant-design/icons";
import { camelCase, set } from "lodash";
import { InfoCircleOutlined } from "@ant-design/icons";
import { CheckCircleOutlined } from "@ant-design/icons";
import { uploadProjectLegendColor } from "constants/CommonConstants";
import { BAR_COLORS, BULK_UPLOAD_HEADING } from "constants/ManageEmployeeConstants";
import Legend from "../../../../../components/shared-components/Legends/LegendMarks";
import useIntlMessage from "hooks/useIntlMessage";

const BulkUploadEmployee = ({ response, visible, onCancel, handleRefresh }) => {
  const getMessage = useIntlMessage();
  const [Loading, setLoading] = useState(false);
  const [TableData, setTableData] = useState([]);
  const [Header, setHeader] = useState([]);
  const [progress, setprogress] = useState(0);
  const [keys, setKeys] = useState([]);
  useEffect(() => {
    handleResponse();
  }, []);

  const onCancelHandler = () => {
    onCancel();
    handleRefresh();
  };
  const increaseprogress = (lines, totalRecords) => {
    const progress = Math.floor((lines / totalRecords) * 100);
    setprogress(progress);
  };

  const handleResponse = () => {
    try {
      if (!Utils.isEmpty(response)) {
        setLoading(true);
        const reader = response.body.getReader();
        const decoder = new TextDecoder("utf-8");
        let buffer = "";
        let rows = [];
        let totalLines = 0;
        let totalRecords = 0;
        let rowsTobeAdded = [];
        const processResult = result => {
          buffer += decoder.decode(result.value || new Uint8Array(), { stream: !result.done });
          const lines = buffer.split("\n");
          buffer = lines.pop();
          totalLines += lines.length;
          for (const line of lines) {
            if (!Utils.isEmpty(line)) {
              const data = JSON.parse(line.replace("data:", ""));

              rows.push(data);
              if (rows.length >= 1) {
                setHeader(rows[0].body?.records?.recordMap);
                getKeys(rows[0].body?.records?.recordMap);
                totalRecords = rows[0].body.records.recordsCount;
                if (!data.body.records.recordIsSync && data.body.records.attributeDtoList?.length !== 0) {
                  rowsTobeAdded.push(data);
                  setTableData(rowsTobeAdded);
                }
              }
            }
          }
          if (result.done) {
            setLoading(false);
            return;
          }
          increaseprogress(totalLines, totalRecords);
          return reader.read().then(processResult);
        };
        return reader.read().then(processResult);
      }
    } catch (error) {
      console.error("Something went wrong while uploading document", error);
    }
  };
  const columnsNew =
    Header &&
    Object.entries(keys).map(col => {
      return {
        title: col[0],
        key: col[0],
        dataIndex: col[1],
        render: (text, record) => {
          return record.body.records[col?.[1]];
        },
      };
    });
  const getKeys = obj => {
    const reversedObj = {};
    for (const key in obj) {
      if (Object.hasOwnProperty.call(obj, key)) {
        reversedObj[obj[key]] = key;
      }
    }
    setKeys(reversedObj);
    return reversedObj;
  };
  const columnsWithInfo = columnsNew && [
    ...columnsNew,
    {
      title: "Info",
      dataIndex: "Info",
      key: "Info",
      render: (text, record) => (
        <div>
          <Tooltip title={record?.body?.records?.message}>
            {record.statusCodeValue === 400 ? (
              <InfoCircleOutlined style={{ color: "red", fontSize: 25 }} />
            ) : (
              <CheckCircleOutlined style={{ color: "green", fontSize: 25 }} />
            )}
          </Tooltip>
        </div>
      ),
    },
  ];

  return (
    <Modal
      title={getMessage(BULK_UPLOAD_HEADING.name)}
      centered
      width="90%"
      visible={visible}
      onCancel={onCancelHandler}
      onOk={onCancelHandler}
    >
      <Row className="mb-4">
        <Progress
          className="mt-5 mb-2"
          strokeColor={{
            "0%": BAR_COLORS[0],
            "100%": BAR_COLORS[1],
          }}
          percent={progress}
        />
      </Row>

      {TableData && <Table columns={columnsWithInfo} dataSource={TableData.slice(1)} loading={Loading} />}
    </Modal>
  );
};

export default BulkUploadEmployee;
